.archive .quote a {
  position: relative;
  display: block;
  overflow: hidden;
  background-color: #212121;
  text-align: center;
  outline: none;
  text-decoration: none;
  transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;
}

.archive .quote a:before {
  content: '';
  position: absolute;
  top: 10px;
  left: 10px;
  right: 10px;
  bottom: 10px;
  border: 1px solid rgba(255, 255, 255, 0.25);
}

.archive .quote a:active, .archive .quote a:focus, .archive .quote a:hover {
  color: #cfbede;
}

.archive .quote a:active .bg, .archive .quote a:focus .bg, .archive .quote a:hover .bg {
  opacity: 0.25;
  transform: translate3d(0, 0, 0);
}

.archive .quote a:active span, .archive .quote a:focus span, .archive .quote a:hover span {
  color: #cfbede;
}

.archive .quote a:active .overlay-border:before,
.archive .quote a:active .overlay-border:after, .archive .quote a:focus .overlay-border:before,
.archive .quote a:focus .overlay-border:after, .archive .quote a:hover .overlay-border:before,
.archive .quote a:hover .overlay-border:after {
  transform: scale(1, 1);
  border-color: #cfbede;
}

.archive .quote .bg {
  display: block;
  width: calc(100% + 30px);
  min-height: 400px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  opacity: 0.35;
  transform-style: preserve-3d;
  transform: translate3d(-30px, 0px, 0px);
  transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
}

.archive .quote .quote-description {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  max-width: 85%;
  margin: 0 auto;
  transform: translateY(-50%);
}

.archive .quote .quote-description img {
  margin-bottom: 30px;
}

.archive .quote .quote-description span {
  display: block;
