  color: #ffffff;
  text-transform: uppercase;
}

.archive .quote .quote-description p {
  margin: 0 0 30px;
  font-size: 14px;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.5);
}

.archive .quote .quote-description p:last-child {
  font-size: 18px;
}

.archive .quote .overlay-border {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.archive .quote .overlay-border:before, .archive .quote .overlay-border:after {
  content: '';
  position: absolute;
  top: 10px;
  right: 10px;
  bottom: 10px;
  left: 10px;
  z-index: 3;
  display: block;
  transition: transform 0.35s ease 0s;
}

.archive .quote .overlay-border:before {
  border-top: 1px solid #ffffff;
  border-bottom: 1px solid #ffffff;
  transform: scale(0, 1);
}

.archive .quote .overlay-border:after {
  border-left: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
  transform: scale(1, 0);
}

